/**/
html{
  font-size: 14px;
}

@media (max-width: 400px) {
  html{
    font-size:8px;
  }
  #header{
    .navbar-brand{
      padding: 15px 0;
    }
    .navbar-brand img{
      width: 120px;
      height: auto;
    }
  }
  #main{       
    .title {
      h1{
        font-size: 5em !important;
      }
    }

  }

}

@media (max-width: 600px) {
  html{
    font-size:10px;
  }
  #main{
    min-height: 700px !important;    
  }
}


body{
  font-family: 'Roboto', sans-serif;
  color: #444444;
  font-size: 1em;

}



#header{
  position: fixed;
  top: 0;
  opacity: 1;
  transition: 0.2s;
  width: 100%;
  background: #fff;
  text-transform: uppercase;
  /*min-height: 75px;*/
  padding-bottom: 10px;
  padding-left:50px;
  padding-right:50px;
  line-height: 75px;
  border: none;
  font-size: 1.21em;
  .navbar-collapse, .navbar-form {
    border: none;
    margin-top: 15px;    
  }
}

@media (max-width: 991px) {
  #header{
    font-size:12px;
    .navbar-nav>li>a{
      padding-left:5px;
      padding-right: 5px;
    }
    .navbar-toggle{
      margin-top:15px;
      margin-bottom: 15px;
    }
  }
}

#header.scroll{
  opacity: 0;

}
#main{
  position: relative;
  /*background: #ad2473 url(../img/bg.png) no-repeat;
  background-size: cover;  */
  height: 100vh;
  min-height: 950px;
  border-bottom: 20px solid #ad2473;
  overflow: hidden;
   video{
      position: absolute;
      top: 50%;
      left: 50%;
      min-width: 100%;
      min-height: 100%;
      width: auto;
      height: auto;
      z-index: -100;
      transform: translateX(-50%) translateY(-50%);
      background: url('../img/bg.png') no-repeat;
      background-size: cover;
      transition: 1s opacity;
    }

  .title{
    z-index: 10;
    position: absolute;
    top:90px;
    left: 0;
    width: 100%;
    text-align: center;
    color: #fff;
    h1{
      font-weight: bold;
      font-size: 7.85em;
      text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.6);
      margin: 0;
    }
    p{
      font-weight: 300;
      font-size: 3.42em;
      text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.6);
      margin: 20px 0 40px 0;
    }
    .order-button{
      font-size: 2.14em;
      font-weight: bold;
      display: inline-block;
      background: #ffffff77;
      border: 1px solid #fff;
      border-radius: 60px;
      padding: 10px 50px;
      &:any-link{
        text-decoration: none;
        color: #fff;
        transition: 0.2s;
      }
      &:hover{
        background: #ffffff99;
      }
    }
  }
}
#carousel{
  z-index: 1;
  position: absolute;
  bottom: 0;
}

section{
  overflow: hidden;
  position: relative;
  display: block;
  text-align: center;
  padding: 25px 0 25px 0;
  h2{
    text-align: center;
    font-size: 5.35em;
    font-weight: 500;
    color: #444444;
    margin: 0;
  }
}
#level1{
  background: #f7f7f7cc;
  min-height: 970px;
  overflow: hidden;
  z-index: 100;
  h2{
    margin-bottom: 65px;
    position: relative;
    z-index: 9999;
    color: #44444400;
    transition: 0.6s;
  }
  h3{
    white-space: nowrap;
    font-weight: 500;
    font-size: 1.57em;
  }
  .thumbnail{
    opacity: 0;
    transition: 0.6s;
    float:left;
  }
  &.scr{
    h2{
      color: #444444ff;
      transform: translate3d(0px,20px,0px);
    }


    .thumbnail{
      opacity: 1;
      margin:0;      
    }
  }
  .service-elem{
    transition: all 0.5s;   
    &.first{z-index: 13;}
    &.second{z-index: 12;}
    &.third{z-index: 11;}
    
  }
  .service-elem:hover{
    left:0;      
    z-index: 99;
    transition: all 0.5s;
      .service-detail{          
        transform: translate3d(0px,0px,0px);
        //width:200%;      
      }    
    }
  .service-elem.second:hover{
      transform: translateX(-100%);      
  }
  .service-elem.third:hover{
      transform: translateX(-200%);
  }
  .service-detail-container{
    position: absolute;
    width:200%;
    overflow: hidden;
    left:100%;
    height: 100%;
    top:0;
    margin-left: -15px;        
    transform: translateX(0px,0px,0px);    
    z-index: 99;
    pointer-events: none;
  }
  .service-detail{
    pointer-events: all; 
    transition: all 0.5s;        
    float:left;
    width:100%;
    transform: translate3d(-100%,0px,0px);
    background: #fff;        
    padding:0 15px;    
    height: 100%;
    font-size:1.28em;
    text-align: left;
    background:#fff url(../img/bck.png) center center no-repeat;
    
    background-size: cover;      
    .service-top{
      float:left;
      height:50%;
      width:100%;      
      ul{
        margin-top:65px;
        list-style: none;
        li{
          background: url('../img/bullet.png') left center no-repeat;
          background-size: 22px;
          padding-left:32px;
          color:#555;
          list-height:27px;
        }
      }
    }
    .service-bottom{
      float:left;
      width:100%;
      height: 50%;    
      .sb-text{
          height:50%;          
          padding-left:40px;
          color:#000;
          table{
            height:100%;
          }
      }
        ul{
          margin:0;
          list-style: none;
          li{
              display: inline;
              margin-right: 5px;          
          }
        }
    }
  }
  .img-slider{
    float:left;
    width:100%;
    position: relative;
    padding:0 40px;
    img{
        height:115px;
      }
    .button{
      position: absolute;      
      width:26px;
      height: 115px;      
      top:0;
      &.slick-prev{
        left:-40px;
        background: url(../img/gal-left.png) left center no-repeat;
      }
      &.slick-next{
        right:-40px;        
        background: url(../img/gal-right.png) left center no-repeat;
      }
    }
  }
}

@media (max-width: 1199px) {
  #level1{
    font-size: 11px;    
    .img-slider{
      img{
        height:90px;
      }
      .button{
        height:90px;
      }
    }
  }  
}

@media (max-width: 991px) {
  #level1.scr{
    font-size:9px;
    .service-elem{
    pointer-events: none;      
    }
    .service-detail-container{    
    margin-left: 0px;    
    }
    .service-elem.second:hover{
      transform: translateX(0%);      
    }
    .service-elem.third:hover{
        transform: translateX(0%);
    }
    .thumbnail{      
      margin-bottom:20px;
      clear: both;      
      width:33%;
      position: relative;
      .service-detail{
        pointer-events: none;
        transform: translate3d(0,0,0);        
        .service-bottom{
           .sb-text{
            height:70%;
           }
        }
      }      
    }
    .img-slider{      
      img{
        height:auto;
      }
      .button{
        height:20px;
        pointer-events: all;
      }
    }
  }
}

@media (max-width: 600px) {
  #level1.scr{
    .img-slider{
      display: none;
    }  
    .thumbnail{
      width:100%;
      img{
        display: none;
      }
      .service-detail-container{ 
        float:left;
        position: relative;
        width:100%;
        left:0;
        height: auto;
        .service-detail{
          padding-bottom: 20px;
          background: #fff;
          .service-top{
            ul{
              margin-top:0;
            }
          }
        }
      }
    }
  }
  
}

#pl1{
  position: absolute;
  transition: 0s;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
  background: #f7f7f7 url(../img/parallax/1.jpg) no-repeat center center;
  opacity: 0.2;
  background-size: cover;
}

#pl2{
  position: absolute;
  transition: 0s;
  top:0;
  left:0;
  width: 100%;
  height: 200%;
  background: #f7f7f7 url(../img/parallax/2.jpg) no-repeat center center;
  opacity: 0.2;
  background-size: cover;
}

#pl3{
  position: absolute;
  transition: 0s;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
  background: #f7f7f7 url(../img/parallax/3.jpg) no-repeat center center;
  opacity: 0.2;
  background-size: cover;
}

#level2{
  h2{
    margin-bottom: 85px;
    color: #44444400;
    transition: 0.6s;
  }
  h3{
    font-weight: 500;
    font-size: 1.42em;
  }
  .thumbnail{
    opacity: 0;
    transition: 0.8s;
    margin-bottom:0;
  }
  &.scr{
    h2{
      color: #444444ff;
      transform: translate3d(0px,20px,0px);
    }
    .thumbnail{
      opacity: 1;
      transform: translate3d(0px,-20px,0px);
    }
  }
}

#level3{
  background: #f7f7f7cc;
 min-height: 520px;
  h2{
    margin-bottom: 85px;
    color: #44444400;
    transition: 0.6s;
  }
  p{
    display: block;
    margin: 0 0 40px 0;
    font-size: 1.42em;
    opacity: 0;
    transition: 0.6s;
  }
  .thumbnail{
    background: transparent;
    opacity: 0;
    transition: 0.8s;
  }

  &.scr{
    h2{
      color: #444444ff;
      transform: translate3d(0px,20px,0px);
    }
    .thumbnail{
      opacity: 1;
    }
    p{
      opacity: 1;
      transform: translate3d(0px,-20px,0px);
    }
  }



}

#level4{
  background: #ad2473;
  color: #fff;
  min-height:100vh;  
  table{
    height:100%;
    width:100%;
  }
  h2{
    color: #ffffff00;
    margin-bottom: 80px;
    transition: 0.6s;
  }
  .left-block{
    position: relative;
    left: -20px;
    opacity: 0;
    transition: 1s;
  }
  .right-block{
    position: relative;
    right: -20px;
    opacity: 0;
    transition: 1s;
  }

  &.scr{
    h2{
      color: #ffffffff;
      transform: translate3d(0px,20px,0px);
    }
    .left-block{
      opacity: 1;
      transform: translate3d(20px,0px,0px);
    }
    .right-block{
      opacity: 1;
      transform: translate3d(-20px,0px,0px);
    }
    #submit{
      opacity: 1;
    }

  }

  .form-block{
    font-weight: 500;
    background: #bd508f;
    padding: 35px;
    margin-bottom: 30px;
    input, label, textarea{
      width: 100%;

    }
    input, textarea{
      border: 2px solid #ad2473;
      padding: 10px;
      color: #000;
    }
    textarea{
      height: 313px;
    }
    label{
      text-align: left;
      margin-bottom: 25px;
      text-transform: uppercase;
      font-size: 1.7em;
      font-weight: normal;
    }
    #file, #email, #name{
      margin-bottom: 25px;
    }
    #file{
      width: 100%;
    }
  }
  button{
    background:  #ad2473;
    color: #fff;
    border: 1px solid #d796bc;
    padding: 10px;
  }
  #submit{
    padding: 10px 30px;
    opacity: 0;
    transition: 0.8s;

  }
}

#level5{  
  min-height:100vh;
  h2{
    color: #44444400;
    transition: 0.6s;
  }
  p{
    font-size: 2em;
    margin: 50px 0 20px 0;
    opacity: 0;
    transition: 0.8s;
  }
  .contacts{
    text-align: left;
    margin: 20px 0;
    opacity: 0;
    transition: 0.8s;
  }
  &.scr{
    h2{
      color: #444444ff;
      transform: translate3d(0px,20px,0px);
    }
    p, .contacts{
      opacity: 1;
    }
  }
}
#map{
  height: 700px;
  margin: 0;
  padding: 0;
}
footer{
  background: #090909;
  text-align: center;
  padding: 100px 0 20px 0;
  text-transform: uppercase;
  .social{
    margin-bottom: 20px;
  }
  .btn-pay{
    background:  transparent;
    color: #fff;
    border: 1px solid #ad2473;
    padding: 10px;
    margin-bottom: 20px;
  }
  .sm-menu{
    display: inline;
    list-style: none;    
    li{
      line-height: 40px;
      padding:0 10px;
      display: inline;
      a{
        text-decoration: underline;
        color:#555;
        &:hover{
          text-decoration:none;
        }
      }
    }
  }
}

.thumbnail{
  background: #fff;
  border: none;
  padding: 0;
}
